import { Text, View, StyleSheet } from 'react-native'
import React, { Component } from 'react'
import { COLORS, FONTS } from '../../../../components/src/AppGlobals'
import FastImage from 'react-native-fast-image'
import { IMessage } from '../PmChatViewController'
import { getTimeFromDateString } from '../../../../framework/src/BlockHelper'
import { defaultProfileImage } from '../../../../framework/src/AppConstant'

export interface Props {
    messageData: IMessage;
}
export class Message extends Component<Props> {
    constructor(props: Props) {
        super(props)
    }
    message = this.props.messageData.attributes;
    render() {
        return (
            <View style={[styles.container, {
                alignSelf: this.message.profile === null ? 'flex-end' : 'flex-start',
                marginRight: this.message.profile === null ? 10 : 0,
                backgroundColor: this.message.profile === null ? COLORS.blue :COLORS.windowBackground,
            }]}>
                {(this.message.image !== null) &&
                    <FastImage source={{ uri: this.message.image ?? defaultProfileImage("user") }}
                        style={styles.imgUpload} >
                    </FastImage>
                }
                <View style={{ flexDirection: 'row' }}>
                    {this.message.profile && <View style={{ borderRadius: 20 }}>
                        <FastImage source={{ uri: this.message?.profile?.profile_pic !== null ? this.message?.profile?.profile_pic : defaultProfileImage("user") }}
                            style={styles.imgUserPic} />
                    </View>}
                    
                    <View style={{ flex: 1 }}>
                        <View style={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', marginBottom: 8 }}>
                            <Text style={{ color: COLORS.textColor, fontFamily: FONTS.bold }}>{this.message?.profile?.user_name}</Text>
                        
                            <View style={{ backgroundColor: '#141414', padding: 5, borderRadius: 15, paddingHorizontal: 15 }}>
                                <Text style={{ color: COLORS.textColor, fontSize: 12 }}>{getTimeFromDateString(this.message.message.created_at)}</Text>
                            </View>
                        </View>
                        <Text style={{ color: COLORS.textColor, fontSize: 13, lineHeight: 18, letterSpacing: 0.8 }}>{this.props.messageData.attributes.message.message}</Text>
                    </View>
                </View>
            </View>
        )
    }
}

const styles = StyleSheet.create({
    container: {
        
        borderRadius: 20,
        borderWidth: 4,
        width: '80%',
        borderColor: '#141414',
        padding: 10,
    },
    imgUserPic: {
        height: 30,
        width: 30,
        borderRadius: 20,
        borderWidth: 1,
        borderColor: COLORS.textColorOffWhite,
        marginRight: 10
    },
    imagePlus: {
        height: 25,
        width: 25,
    },
    imgUpload: {
        width: "100%",
        height: 250,
        borderTopLeftRadius: 20,
        borderTopRightRadius: 20,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: 'rgba(0,0,0,0.1)',
        marginBottom: 20
    },
    viewUploadPlusImg: {
        justifyContent: 'center',
        alignItems: 'center',
        height: 45,
        width: 45,
        borderRadius: 45,
        backgroundColor: 'rgba(0,0,0,0.5)'
    },
})
export default Message